.section-intro {
  position: absolute;
  top: 160px;
  left: 50%;
  transform: translate(-50%);


  &__heading {
    font-size: 64px;
    font-weight: 900;
    color: white;
    margin-bottom: 22px;
    white-space: nowrap;
  }

  &__text {
    font-size: 24px;
    color: white;
    margin-bottom: 42px;
  }

  &__button {
    width: 226px;
    height: 56px;
    background-color: white;
    border-radius: 64px;
    font-weight: bold;
  }
}
